'use client';

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import { HiChevronDown } from "react-icons/hi";
import { headerStyles } from "./styles";
import api from "@/lib/api"; // Ajuste o caminho conforme sua estrutura

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isCheckingAuth, setIsCheckingAuth] = useState(true);
  const [activeDropdown, setActiveDropdown] = useState<string | null>(null);
  const [isMobile, setIsMobile] = useState(false);
  
  const router = useRouter();
  const pathname = usePathname();
  const dropdownRef = useRef<HTMLUListElement>(null);

  // Efeitos
  useEffect(() => {
    checkAuthentication();
    
    const handleResize = () => {
      const mobile = window.innerWidth <= 1024;
      setIsMobile(mobile);
      if (!mobile) {
        setMenuOpen(false);
        setActiveDropdown(null);
      }
    };
    
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setActiveDropdown(null);
      }
    }
    
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  // Handlers
  const checkAuthentication = async (): Promise<void> => {
    try {
      const response = await api.get("/v1/Auth/CheckAuth");
      setIsAuthenticated(response.data.authenticated);
    } catch (error) {
      console.error("Erro ao verificar autentica√ß√£o:", error);
      setIsAuthenticated(false);
    } finally {
      setIsCheckingAuth(false);
    }
  };

  const toggleMenu = (): void => {
    setMenuOpen(!menuOpen);
    if (!menuOpen) setActiveDropdown(null);
  };

  const closeMenu = (): void => {
    setMenuOpen(false);
    setActiveDropdown(null);
  };

  const toggleDropdown = (dropdownName: string): void => {
    setActiveDropdown(activeDropdown === dropdownName ? null : dropdownName);
  };

  const handleLogout = async (): Promise<void> => {
    try {
      await api.post("/v1/Auth/Logout");
      setIsAuthenticated(false);
      closeMenu();
      router.push("/");
      // showAlert("Logout realizado com sucesso!", "success");
    } catch (error) {
      console.error("Erro ao fazer logout:", error);
      // showAlert("Erro ao fazer logout.", "error");
    }
  };

  // Dados dos menus
  const menuItems = [
    { href: "/", label: "In√≠cio" },
    { href: "/etapas", label: "Etapas" },
  ];

  const galleryItems = [
    { href: "/galeria/imagem", label: "Imagens" },
    { href: "/galeria/video", label: "Videos" },
  ];

  const candidaturaItems = [
    { href: "/candidaturas/formulario", label: "Formul√°rio" },
    { href: "/candidatura/estado", label: "Estado" },
  ];

  const profileItems = [
    { href: "/perfil/dados", label: "Meus Dados" },
    { href: "/perfil/editar", label: "Editar Perfil" },
    { href: "/perfil/senha", label: "Alterar Senha" },
  ];

  if (isCheckingAuth) {
    return (
      <header className={headerStyles.layout.header}>
        <div className={headerStyles.layout.container}>
          <nav className={headerStyles.layout.nav}>
            <div className={headerStyles.logo.container}>
              <Link href="/" className="flex items-center">
                {/* <img src={logo} alt="logo" className={headerStyles.logo.image} /> */}
                <span className="ml-3 text-xl font-bold text-gray-800">Condom√≠nio Osvaldo MJ</span>
              </Link>
            </div>
            <div className={headerStyles.loading}>Carregando...</div>
          </nav>
        </div>
      </header>
    );
  }

  return (
    <header className={headerStyles.layout.header}>
      <div className={headerStyles.layout.container}>
        <nav className={headerStyles.layout.nav}>
          
          {/* Logo */}
          <div className={headerStyles.logo.container}>
            <Link href="/" className="flex items-center">
              {/* <img src={logo} alt="logo" className={headerStyles.logo.image} /> */}
              <span className="ml-3 text-xl font-bold text-gray-800">Condom√≠nio Osvaldo MJ</span>
            </Link>
          </div>

          {/* Menu Desktop */}
          <ul className={headerStyles.menu.desktop}>
            {menuItems.map((item) => (
              <li key={item.href} className={headerStyles.menu.item}>
                <Link 
                  href={item.href}
                  className={`${headerStyles.menu.link} ${headerStyles.menu.linkUnderline}`}
                >
                  {item.label}
                </Link>
              </li>
            ))}

            {/* Dropdown Galeria */}
            <li className={`${headerStyles.menu.item} group`}>
              <div className={headerStyles.dropdown.trigger}>
                <span className={headerStyles.dropdown.title}>
                  Galeria <HiChevronDown className={`${headerStyles.dropdown.arrow} group-hover:${headerStyles.dropdown.arrowOpen}`} />
                </span>
              </div>
              <ul className={headerStyles.dropdown.menu}>
                {galleryItems.map((item) => (
                  <li key={item.href}>
                    <Link href={item.href} className={headerStyles.dropdown.menuItem}>
                      {item.label}
                    </Link>
                  </li>
                ))}
              </ul>
            </li>

            {/* Dropdown Candidatura (autenticado) */}
            {isAuthenticated && (
              <li className={`${headerStyles.menu.item} group`}>
                <div className={headerStyles.dropdown.trigger}>
                  <span className={headerStyles.dropdown.title}>
                    Candidatura <HiChevronDown className={`${headerStyles.dropdown.arrow} group-hover:${headerStyles.dropdown.arrowOpen}`} />
                  </span>
                </div>
                <ul className={headerStyles.dropdown.menu}>
                  {candidaturaItems.map((item) => (
                    <li key={item.href}>
                      <Link href={item.href} className={headerStyles.dropdown.menuItem}>
                        {item.label}
                      </Link>
                    </li>
                  ))}
                </ul>
              </li>
            )}

            {/* Dropdown Perfil (autenticado) */}
            {isAuthenticated && (
              <li className={`${headerStyles.menu.item} group`}>
                <div className={headerStyles.dropdown.trigger}>
                  <span className={headerStyles.dropdown.title}>
                    Perfil <HiChevronDown className={`${headerStyles.dropdown.arrow} group-hover:${headerStyles.dropdown.arrowOpen}`} />
                  </span>
                </div>
                <ul className={headerStyles.dropdown.menu}>
                  {profileItems.map((item) => (
                    <li key={item.href}>
                      <Link href={item.href} className={headerStyles.dropdown.menuItem}>
                        {item.label}
                      </Link>
                    </li>
                  ))}
                </ul>
              </li>
            )}

            {/* Bot√£o Entrar/Sair */}
            <li>
              {isAuthenticated ? (
                <button 
                  onClick={handleLogout}
                  className={`${headerStyles.buttons.base} ${headerStyles.buttons.logout}`}
                >
                  Sair
                </button>
              ) : (
                <button className={`${headerStyles.buttons.base} ${headerStyles.buttons.primary}`}>
                  <Link href="/login">Entrar</Link>
                </button>
              )}
            </li>
          </ul>

          {/* Menu Mobile Toggle */}
          <div 
            className={headerStyles.toggle.container}
            onClick={toggleMenu}
          >
            <span className={`${headerStyles.toggle.bar} ${menuOpen ? headerStyles.toggle.barOpen[0] : ''}`}></span>
            <span className={`${headerStyles.toggle.bar} my-1.5 ${menuOpen ? headerStyles.toggle.barOpen[1] : ''}`}></span>
            <span className={`${headerStyles.toggle.bar} ${menuOpen ? headerStyles.toggle.barOpen[2] : ''}`}></span>
          </div>
        </nav>
      </div>

      {/* Menu Mobile */}
      <div className={`${headerStyles.menu.mobile} ${menuOpen ? headerStyles.menu.mobileOpen : headerStyles.menu.mobileClosed}`}>
        <div className="pt-20 pb-6 px-6 h-full overflow-y-auto">
          <ul className="space-y-4" ref={dropdownRef}>
            {menuItems.map((item) => (
              <li key={item.href}>
                <Link 
                  href={item.href}
                  className="block py-3 text-lg text-gray-800 hover:text-orange-500 font-medium border-b border-gray-200"
                  onClick={closeMenu}
                >
                  {item.label}
                </Link>
              </li>
            ))}

            {/* Mobile Dropdowns */}
            <li>
              <div 
                className="flex items-center justify-between py-3 text-lg text-gray-800 font-medium border-b border-gray-200 cursor-pointer"
                onClick={() => toggleDropdown('galeria')}
              >
                Galeria
                <HiChevronDown className={`w-5 h-5 transition-transform ${activeDropdown === 'galeria' ? 'rotate-180' : ''}`} />
              </div>
              {activeDropdown === 'galeria' && (
                <ul className="ml-4 mt-2 space-y-2">
                  {galleryItems.map((item) => (
                    <li key={item.href}>
                      <Link 
                        href={item.href}
                        className="block py-2 text-gray-600 hover:text-orange-500"
                        onClick={closeMenu}
                      >
                        {item.label}
                      </Link>
                    </li>
                  ))}
                </ul>
              )}
            </li>

            {isAuthenticated && (
              <>
                <li>
                  <div 
                    className="flex items-center justify-between py-3 text-lg text-gray-800 font-medium border-b border-gray-200 cursor-pointer"
                    onClick={() => toggleDropdown('candidatura')}
                  >
                    Candidatura
                    <HiChevronDown className={`w-5 h-5 transition-transform ${activeDropdown === 'candidatura' ? 'rotate-180' : ''}`} />
                  </div>
                  {activeDropdown === 'candidatura' && (
                    <ul className="ml-4 mt-2 space-y-2">
                      {candidaturaItems.map((item) => (
                        <li key={item.href}>
                          <Link 
                            href={item.href}
                            className="block py-2 text-gray-600 hover:text-orange-500"
                            onClick={closeMenu}
                          >
                            {item.label}
                          </Link>
                        </li>
                      ))}
                    </ul>
                  )}
                </li>

                <li>
                  <div 
                    className="flex items-center justify-between py-3 text-lg text-gray-800 font-medium border-b border-gray-200 cursor-pointer"
                    onClick={() => toggleDropdown('perfil')}
                  >
                    Perfil
                    <HiChevronDown className={`w-5 h-5 transition-transform ${activeDropdown === 'perfil' ? 'rotate-180' : ''}`} />
                  </div>
                  {activeDropdown === 'perfil' && (
                    <ul className="ml-4 mt-2 space-y-2">
                      {profileItems.map((item) => (
                        <li key={item.href}>
                          <Link 
                            href={item.href}
                            className="block py-2 text-gray-600 hover:text-orange-500"
                            onClick={closeMenu}
                          >
                            {item.label}
                          </Link>
                        </li>
                      ))}
                    </ul>
                  )}
                </li>
              </>
            )}

            {/* Bot√£o Mobile */}
            <li className="pt-6">
              {isAuthenticated ? (
                <button 
                  onClick={handleLogout}
                  className={`w-full ${headerStyles.buttons.base} ${headerStyles.buttons.logout}`}
                >
                  Sair
                </button>
              ) : (
                <button className={`w-full ${headerStyles.buttons.base} ${headerStyles.buttons.primary}`}>
                  <Link href="/login" onClick={closeMenu}>Entrar</Link>
                </button>
              )}
            </li>
          </ul>
        </div>
      </div>
    </header>
  );
}